 footer {
  display: none; }

main {
  width: auto;
  max-width: none;
  margin: 0; }

@page {
  margin: 2cm; }

h1 {
  margin: 1rem 0; }

img {
  max-width: 100%; }

a {
  text-decoration: none;
  color: #161715; }

/**
 * VS theme by Andrew Lock (https://andrewlock.net)
 * Inspired by Visual Studio syntax coloring
 */
code[class*="language-"],
pre[class*="language-"] {
  color: #393A34;
  font-family: "Consolas", "Bitstream Vera Sans Mono", "Courier New", Courier, monospace;
  direction: ltr;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  font-size: 0.95em;
  line-height: 1.2em;
  -moz-tab-size: 4;
  -o-tab-size: 4;
  tab-size: 4;
  -webkit-hyphens: none;
  -moz-hyphens: none;
  -ms-hyphens: none;
  hyphens: none; }

pre[class*="language-"]::-moz-selection, pre[class*="language-"] ::-moz-selection,
code[class*="language-"]::-moz-selection, code[class*="language-"] ::-moz-selection {
  background: #C1DEF1; }

pre[class*="language-"]::selection, pre[class*="language-"] ::selection,
code[class*="language-"]::selection, code[class*="language-"] ::selection {
  background: #C1DEF1; }

/* Code blocks */
pre[class*="language-"] {
  padding: 1em;
  margin: .5em 0;
  overflow: auto;
  border: 1px solid #dddddd;
  background-color: white; }

/* Inline code */
:not(pre) > code[class*="language-"] {
  padding: .2em;
  padding-top: 1px;
  padding-bottom: 1px;
  background: #f8f8f8;
  border: 1px solid #dddddd; }

.token.comment,
.token.doctype {
  color: #008000;
  font-style: italic; }

.token.string {
  color: #A31515; }

.token.punctuation,
.token.operator {
  color: #393A34;
  /* no highlight */ }


.token.number,
.token.variable {
  color: #36acaa; }


.token.keyword,
.token.attr-value,
code[class*="language-css"] {
  color: #0000ff; }

.token.function {
  color: #393A34; }

.token.selector {
  color: #00009f; }

.token.important {
  font-weight: bold; }

.token.class-name {
  color: #2B91AF; }

.token.tag,
.token.selector {
  color: #800000; }

.token.attr-name,
.token.property {
  color: #ff0000; }

